var peg = require('pegjs');

var _require = require('./FieldRegexpFactory'),
    FieldParser = _require.FieldParser;

var syntax = "\n    start = eol fields:(complexField/simpleField)* {return fields}\n\n    simpleField = header:fieldHeader fieldText:content {\n                          return {\n                            type: header.type,\n                            option: header.option,\n                            fieldValue: fieldText,\n                            content: header.text+fieldText\n                          };\n                        }\n    \n    complexField = header:fieldHeader \":\" qualifier:$(!\"/\" .)+ \"//\" fieldText:content {\n                          var fieldValue = \":\"+qualifier+\"//\"+fieldText;\n                          return {\n                            type: header.type,\n                            option: header.option,\n                            fieldValue: fieldValue,\n                            content: header.text+fieldValue\n                          };\n                        }\n    \n    \n    fieldHeader = \":\" type:$(digit digit) option:letter? \":\" {return {type: type, option: option === null ? undefined : option, text: text()}}\n    \n    content = text:$(!((eol \":\")/(eol \"-\")) .)* ((eol &\":\")/(eol \"-\")) {return text}\n    \n    eol = \"\\n\"\n    \n    digit = [0-9]\n    \n    letter = [a-zA-Z]\n    ";

module.exports.parse = function (input, fieldPatterns) {
  if (typeof input.content[0] === 'object') {
    var _result = {};
    input.content.forEach(function (field) {
      var content = field.content;

      if (Array.isArray(field.content) && field.content.length === 1) {
        // eslint-disable-next-line prefer-destructuring
        content = field.content[0];
      }

      _result[field.name] = content;
    });
    return _result;
  }

  var result = peg.generate(syntax).parse(input.content[0]);
  var fieldParser = new FieldParser(fieldPatterns);
  Array.from(result).forEach(function (field, index) {
    var fieldCode = field.type + (field.option != null ? field.option : '');
    var parsedField = fieldParser.parse(fieldCode, field.fieldValue);
    result[index].ast = parsedField;
  });
  return result;
};