var peg = require('pegjs');

var syntax = "\n    start = blocks:blocks [\\n]* {return blocks}\n\n    blocks = block*\n\n    block = \"{\" name:$[^:]+ \":\" content:content \"}\" {return {name:name, content:content || undefined}}\n\n    content = (emptyBlock / block / text )+\n    \n    emptyBlock = \"{\" name:$[^:]+ \":}\" {return {name:name, content: undefined}}\n\n    text = chars:$[^{}]+\n    ";

module.exports.parse = function (input) {
  var result = peg.generate(syntax).parse(input);
  var map = {};
  result.forEach(function (block) {
    if (map["block".concat(block.name)]) {
      if (!map.extraBlocks) {
        map.extraBlocks = {};
      }

      if (map.extraBlocks["block".concat(block.name)]) {
        throw Error("too much extra blocks with type ".concat(block.name));
      }

      map.extraBlocks["block".concat(block.name)] = block;
    } else {
      map["block".concat(block.name)] = block;
    }
  });
  return map;
};