var _ = require('lodash');
/**
 * Generate Swift message from blocks. Blocks order is the same as in array. No
 * [{block: 1, data: value},{block:2, data:2}]
 * @param {array} arrayOfBlocks
 * @returns {*}
 */


function hasAllProps(obj, props) {
  var propsTrue = _.chain(props).map(function (prop) {
    return _.has(obj, prop);
  }).without(false).value();

  return propsTrue.length === props.length;
}

var depthOf = function depthOf(object) {
  var level = 1;
  Object.keys(object).forEach(function (key) {
    // eslint-disable-next-line no-prototype-builtins
    if (object.hasOwnProperty(key) && typeof object[key] === 'object') {
      var depth = depthOf(object[key]) + 1;
      level = Math.max(depth, level);
    }
  });
  return level;
};

function generateJsonDataBlock(data, blockname) {
  var subst = '';

  if (depthOf(data) !== 1) {
    throw new Error("block data ".concat(blockname, " has several depth level, but should 1"));
  }

  Object.keys(data).forEach(function (key) {
    if (data[key] === undefined) {
      subst += "{".concat(key, ":}");
    } else {
      subst += "{".concat(key, ":").concat(data[key], "}");
    }
  });
  return "{".concat(blockname, ":").concat(subst, "}");
}

module.exports = function (arrayOfBlocks) {
  var message = '';

  try {
    if (!Array.isArray(arrayOfBlocks)) {
      throw new Error('invalid swift message format');
    }

    arrayOfBlocks.forEach(function (block) {
      if (!block.block || !block.data) {
        throw new Error('invalid swift message block');
      } // let data;


      switch (block.block) {
        case 1:
          {
            /**
             * generate block 1
             */
            var data = block.data;
            /**
             * if data is string, than take it as is
             */

            if (typeof data === 'string') {
              message += "{1:".concat(data, "}");
              break;
            }
            /**
             * if data is object, validate that we have required keys and generate block
             */


            if (typeof data === 'object') {
              if (hasAllProps(data, ['applicationId', 'serviceId', 'receivingLtId', 'sessionNumber', 'sequenceNumber'])) {
                message += "{1:".concat(data.applicationId).concat(data.serviceId).concat(data.receivingLtId).concat(data.sessionNumber).concat(data.sequenceNumber, "}");
                break;
              }

              throw new Error("Invalid data for block 1. something is missing ".concat(JSON.stringify(data), "}"));
            }

            throw new Error('Invalid block 1 format. It must be a string or object with required fields');
          }

        case 2:
          {
            /**
             * generate block 2
             */
            var _data = block.data;
            /**
             * if data is string, than take it as is
             */

            if (typeof _data === 'string') {
              message += "{2:".concat(_data, "}");
              break;
            }
            /**
             * if data is object, validate that we have required keys and generate block
             */


            if (typeof _data === 'object') {
              if (hasAllProps(_data, ['direction', 'msgType', 'bic', 'prio'])) {
                message += "{2:".concat(_data.direction).concat(_data.msgType).concat(_data.bic).concat(_data.prio, "}");
                break;
              }

              throw new Error("Invalid data for block 2. something is missing ".concat(JSON.stringify(_data)));
            }

            throw new Error('Invalid block 2 format. It must be a string or object with required fields');
          }

        case 3:
          {
            /**
             * generate block 3
             */
            var _data2 = block.data;
            /**
             * block 3 should be an object with key/value pairs
             */

            if (typeof block === 'object') {
              message += generateJsonDataBlock(_data2, 3);
              break;
            }

            throw new Error('Invalid block 3 format. It must be a object with key/value pairs');
          }

        case 4:
          {
            /**
             * generate block 4
             */
            var _data3 = block.data; // eslint-disable-next-line no-param-reassign

            block.strategy = block.strategy || 'content';
            /**
             * block 4 may be an array with fields
             */

            if (Array.isArray(_data3)) {
              var subst = '';

              _data3.forEach(function (field) {
                if (block.strategy === 'content') {
                  subst += "".concat(field.content, "\n");
                }

                if (block.strategy === 'combine') {
                  subst += ":".concat(field.type).concat(field.option, ":").concat(field.fieldValue, "\n");
                }
              });

              message += "{4:\n".concat(subst, "-}");
              break;
            }
            /**
             * block 3 may be an object with key/value pairs
             */


            if (typeof block === 'object') {
              message += generateJsonDataBlock(_data3, 4);
              break;
            }

            throw new Error('Invalid block 4 format. It must be a object with key/value pairs or array');
          }

        case 5:
          {
            /**
             * generate block 5
             */
            var _data4 = block.data;
            /**
             * block 5 should be an object with key/value pairs
             */

            if (typeof block === 'object') {
              message += generateJsonDataBlock(_data4, 5);
              break;
            }

            throw new Error('Invalid block 5 format. It must be a object with key/value pairs');
          }

        case 'S':
          {
            /**
             * generate block S
             */
            var _data5 = block.data;
            /**
             * block S should be an object with key/value pairs
             */

            if (typeof block === 'object') {
              message += generateJsonDataBlock(_data5, 'S');
              break;
            }

            throw new Error('Invalid block S format. It must be a object with key/value pairs');
          }

        default:
          throw new Error("invalid block type ".concat(block.block));
      }
    });
    return message;
  } catch (err) {
    return err;
  }
};